@import './components/Calendar-widget/styles.css';
@import './components/Clock-widget/styles.css';
@import './components/Weather-widget/styles.css';
@import './components/Widget/styles.css';

.App {
	background: #000;
	color: #fff;
	font-size: 5.5vmin;
	max-height: 100vh;
	min-height: 100vh;
	overflow: hidden;
	text-align: center;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
	max-width: 60vmin;
	-moz-user-select: none;
	 -ms-user-select: none;
	     user-select: none;
	-webkit-user-select: none;
}

@media (min-width: 900px) {
	.App {
		/*min-height: auto;*/
		-ms-flex-direction: row;
		    flex-direction: row;
		/*justify-content: center;*/
		-ms-flex-align: center;
		    align-items: center;
		-ms-flex-pack: distribute;
		    justify-content: space-around;
		max-width: none;
	}
}

h1 {
	font-size: 4em;
	margin: 0;
	line-height: 1;
}

h2 {
	font-size: 1.25em;
	margin: 0;
	line-height: 1;
	font-family: 'Open Sans';
	font-weight: 300;
	text-transform: uppercase;
}

h3 {
	font-size: 0.6em;
	line-height: 1;
}

.underline {
	padding-bottom: 1rem;
	position: relative;
}

.underline::after {
	content: '';
	right: 0;
	position: absolute;
	bottom: 0;
	left: 0;
	background: white;
	border-radius: 1em;
	height: 3px;
	-webkit-box-shadow: 0 0 1em #1041FF,
	0 0 3em #1041FF,
	0 0 10em #1041FF,
	0 0 10em #1041FF,
	0 0 .4em #8BFDFE,
	.05em .1em .1em #147280;
	        box-shadow: 0 0 1em #1041FF,
	0 0 3em #1041FF,
	0 0 10em #1041FF,
	0 0 10em #1041FF,
	0 0 .4em #8BFDFE,
	.05em .1em .1em #147280;
}

.sub-widget {
	/*animation: animate-screen-saver 60s infinite;*/
}

@-webkit-keyframes animate-screen-saver {
	0% {
		opacity: 0;
		-webkit-transform: scale(0.5);
		        transform: scale(0.5);
	}
	1.5% {
		opacity: 0.9;
		-webkit-transform: scale(1.1);
		        transform: scale(1.1);
	}
	2% {
		opacity: 1;
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
	90% {
		opacity: 1;
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
	90.5% {
		opacity: 0.9;
		-webkit-transform: scale(1.1);
		        transform: scale(1.1);
	}
	92% {
		opacity: 0;
		-webkit-transform: scale(0.5);
		        transform: scale(0.5);
	}
	100% {
		opacity: 0;
		-webkit-transform: scale(0.5);
		        transform: scale(0.5);
	}
}

@keyframes animate-screen-saver {
	0% {
		opacity: 0;
		-webkit-transform: scale(0.5);
		        transform: scale(0.5);
	}
	1.5% {
		opacity: 0.9;
		-webkit-transform: scale(1.1);
