	}
	90.5% {
		opacity: 0.9;
		-webkit-transform: scale(1.1);
		        transform: scale(1.1);
	}
	92% {
		opacity: 0;
		-webkit-transform: scale(0.5);
		        transform: scale(0.5);
	}
	100% {
		opacity: 0;
		-webkit-transform: scale(0.5);
		        transform: scale(0.5);
	}
}

@-webkit-keyframes animate-popIn {
	0% {
		opacity: 0;
		-webkit-transform: scale(0.5);
		        transform: scale(0.5);
	}
	50% {
		opacity: 0.9;
		-webkit-transform: scale(1.1);
		        transform: scale(1.1);
	}
	100% {
