		        transform: scale(1);
	}
	50% {
		opacity: 0.9;
		-webkit-transform: scale(1.1);
		        transform: scale(1.1);
	}
	100% {
		opacity: 0;
		-webkit-transform: scale(0.5);
		        transform: scale(0.5);
	}
}

@keyframes animate-popOut {
	0% {
		opacity: 1;
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
	50% {
		opacity: 0.9;
		-webkit-transform: scale(1.1);
		        transform: scale(1.1);
	}
	100% {
		opacity: 0;
		-webkit-transform: scale(0.5);
		        transform: scale(0.5);
	}
}


