@import 'helper.min.css';
@import 'pe-icon-set-weather.min.css';

html, body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans Condensed', sans-serif;
  background: black;
}

* {
  font-weight: 300;
}

.neon {
  text-shadow: 0 0 1rem #1041FF,
  0 0 3rem #1041FF,
  0 0 10rem #1041FF,
  0 0 10rem #1041FF,
  0 0 .4rem #8BFDFE,
  .05rem .1rem .1rem #147280;
}

.neonBox {
  -webkit-box-shadow: 0 0 1rem #1041FF,
  0 0 3rem #1041FF,
  0 0 10rem #1041FF,
  0 0 10rem #1041FF,
  0 0 .4rem #8BFDFE,
  .05rem .1rem .1rem #147280;
          box-shadow: 0 0 1rem #1041FF,
  0 0 3rem #1041FF,
  0 0 10rem #1041FF,
  0 0 10rem #1041FF,
  0 0 .4rem #8BFDFE,
  .05rem .1rem .1rem #147280;
}



