.component-clock-widget {
	font-weight: 300;
	/*height: 75vmin;*/
	display: -ms-flexbox;
	display: flex;
	position: relative;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-direction: column;
	    flex-direction: column;
	margin-bottom: 1rem;
}

.component-clock-widget .clock,
.component-clock-widget .day,
.component-clock-widget .date {
	z-index: 2;
	margin: 0;
}

.component-clock-widget .clock::after {
	opacity: 0.5;
}

.component-clock-widget .day {
	margin-top: 0.25em;
}

.component-clock-widget .date {
	font-family: 'Open Sans';
	font-weight: 700;
	margin-top: 0.25em;
}

.component-clock-widget__video {
    position: absolute;
    z-index: 0;
    left: 50%;
    height: auto;
    /*transform: translateX(-50%) scale(1.7);*/
    -webkit-transform: translateX(-50%) scale(1.2);
        -ms-transform: translateX(-50%) scale(1.2);
            transform: translateX(-50%) scale(1.2);
}



